<template>
    <div class="box">
        <el-card class="blur" shadow="always" :body-style="{ padding: '20px' }">
            <template #header>
                <div style="display:flex">
                    <el-input v-model="input" placeholder="输入歌曲ID" style="margin-right:10px"></el-input>
                    <el-button type="primary" @click="search(input)">查询</el-button>
                    <el-button type="warning" @click="all">全部</el-button>
                    <el-button type="danger" @click="result = ''">清空</el-button>
                </div>
            </template>
            <div>
                <el-input type="textarea" v-model="result" :rows="20" placeholder="请输入内容"></el-input>
            </div>
        </el-card>
    </div>
</template>
    
<script setup>
import { ref } from "vue";
import { useStore } from "vuex";
import axios from "axios";
import playlist from "@/data/playlist.json";
const store = useStore();
const input = ref("554241732");
const result = ref("");

const search = id => {
    if (!id) return false;
    axios.get(`${store.state.webService}/song/detail?ids=${id}`).then(res => {
        let item = res.data.songs[0];
        let singer = "";
        item.ar.forEach((i, j) => {
            j > 0 ? (singer += ` / ${i.name}`) : (singer = i.name);
        });
        let pic = item.al.picUrl;
        pic = pic.substring(25, pic.lastIndexOf("."));
        result.value += `
            {
                "id": ${item.id},
                "song": "${item.name}",
                "singer": "${singer}",
                "album": "${item.al.name}",
                "pic": "${pic}",
                "mv": ${item.mv}
            },`;
    });
};
const all = () => {
    playlist.forEach(item => {
        search(item.id);
    });
};
</script>
    
<style lang="less" scoped>
.el-card {
    width: 550px;
    margin: 50px auto;
}
</style>